// 封装请求地图的数据
export async function getmap_http(url, opts) {
  let resda = await axios.get(`./map/${url}`)
  return Promise.resolve(resda)
}

// 封装get请求
// let $url = ($URL.JSON === true ? '/data' : '');
let $url = '/data'

export function get_http(url, opts) {
  // 传入的参数，默认为默认配置DEFAULTS的数据
  let options = Object.assign({}, DEFAULTS, opts)
  return request({
    method: 'get',
    url: $url + BaseApi[url],
    // 传参
    params: options
  })
}

// 封装post请求
export function post_http(url, opts) {
  // 传入的参数，默认为默认配置DEFAULTS的数据
  let options = Object.assign({}, DEFAULTS, opts)
  return request({
    method: 'post',
    url: BaseApi[url],
    // 传参
    data: options
  })
}

/**
 * 封装get_restful请求
 * @param {*} url
 * @param {*} opts
 * @param {*} params
 * 使用：
 *  get_restful_http("dataDetail",{
        path: {
          index: 'index' + getItem('index')
        }
      })
 */
export function get_restful_http(url, opts, params) {
  // 传入的参数，默认为默认配置DEFAULTS的数据
  let options = Object.assign({}, DEFAULTS, params)
  return request({
    method: 'get',
    url: convertRESTAPI(BaseApi[url], opts),
    // 传参
    params: options
  })
}
